"use strict";

var BPromise = require("bluebird");
var secureStore = require("./secure-store");
var TokenStore = require("./Token-store");
//var fingerprint = require("./fingerprint");

var tokenStore = new TokenStore(secureStore);

module.exports = (function() {
	var verify = function(callback) {
		window.plugins.touchid.isAvailable(
		  function() {
			  window.plugins.touchid.verifyFingerprint(
		  		  'Scan your fingerprint please', // this will be shown in the native scanner popup
		  		   function(msg) {
					   callback();
				   }, // success handler: fingerprint accepted
		  		   function(msg) {
					   deleteToken();
				   } // error handler with errorcode and localised reason
			   );
		  }, // success handler: TouchID available
		  function(msg) {
			  deleteToken();
		  } // error handler: no TouchID available
		);

		function deleteToken() {
			BPromise.all([
				tokenStore.remove()
			]).then(closeView);
		}

		function closeView() {
			if (callback) callback();
		}
	};

	/*var deleteToken = function(callback) {
		tokenStore.remove().then(callback);
	};*/

    return {
        verify: verify
    }
})();



